.BoardCell {
  width: auto;
  border-radius: 0.5rem;
  position: relative;
}

.tetromino {
  /* border-top: 0.2rem solid rgba(255, 255, 255, 0.1); */
  /* border-top: 1px; */
  border-left: 0.4rem solid rgba(255, 255, 255, 0.1);
  /* border-bottom: 0.2rem solid rgba(0, 0, 0, 0.1); */
  border-right: 0.4rem solid rgba(0, 0, 0, 0.1);
  /* margin: 0.05rem; */
}

.tetromino .Sparkle {
  position: absolute;
  z-index: 10;
  /* width: 6px;
  height: 6px; */
  width: 0.4rem;
  height: 0.4rem;
  left: -2px;
  top: -2px;

  background-color: rgba(255, 255, 255, 0.4);
  /* border-radius: 4px 4px; */
  border-radius: 1rem 1rem;
}

.tetromino.ghost {
  background-color: rgba(255, 255, 255, 0.2);
  /* border:   0.01rem  solid rgba(255, 255, 255, 0.2); */
  border-color: solid rgba(255, 4, 4, 0.9);
}

.tetromino.ghost .Sparkle {
  opacity: 0;
}

.tetromino__i {
  background-color: rgba(80, 227, 230, 1);
}

.tetromino__j {
  background-color: rgba(36, 95, 223, 1);
}

.tetromino__l {
  background-color: rgba(223, 173, 36, 1);
}

.tetromino__o {
  background-color: rgba(223, 217, 36, 1);
}

.tetromino__s {
  background-color: rgba(80, 227, 230, 1);
}

.tetromino__t {
  background-color: rgba(40, 211, 56, 1);
}

.tetromino__z {
  background-color: rgba(240, 80, 195, 1);
}
